@media (max-width: 1200px) {
 .manage {
  &__box {
   padding-left: 0;
  }
 }
}

@media (max-width: 992px) {
 .menu {
  &--active .menu__list {
   transform: translateY(0);
  }
  &__inner {
   justify-content: space-between;
  }
  &__list,
  &__link {
   display: none;
  }
  &__list  {
   position: absolute;
   background-color: #7792E0;
   display: block;
   text-align: center;
   padding-top: 140px;
   top: 0;
   left: 0;
   right: 0;
   bottom: 0;
   height: 100vh;
   transform: translateY(-300%);
   transition: transform 0.5s ease;
   z-index: 444;
  }
  &__burger {
   display: block;
   z-index: 555;
  }
  &__list-item {
   :hover {
    border-bottom: 1px solid $primaryColor;
   }
   &:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
   }
  }
  &__social-item {
   &:last-child {
    margin-right: 0;
   }
  }
 }

 .logo {
  margin-right: 0;
 }

 .manage {
  padding: 100px 0;
  &__box {
   margin: 0 auto;
  }
  &__box-image {
   display: none;
  }
 }

 .about {
  padding: 100px 0;
 }
}

@media (max-width: 768px) {
 .hero {
  &__image-box {
   display: none;
  }
  &__title {
   font-size: 40px;
  }
  &__text {
  margin-bottom: 30px;
  }
 }

 .customers {
  padding: 40px ;
  &__list {
   display: flex;
   flex-direction: column;
   align-items: center;
  }
  &__item {
   &:not(:last-child) {
    margin-right: 0;
    margin-bottom: 30px;
   }
  }
 }
}

@media (max-width: 576px) {
 .menu {
  &__burger {
   width: 23px;
   height: 23px;
  }
  &__line,
  &__line::before,
  &__line::after {
   width: 23px;
  }
 }
 .hero {
  &__title {
   font-size: 35px;
  }
  &__text {
  margin-bottom: 30px;
  }
 }

 .manage {
  padding: 40px 0;
 }
 
}